.resetPasswordMainDiv {
  display: flex;
  height: 100%;
  justify-content: center;
}
.resetPasswordSubMainDiv {
  width: 35%;
  display: flex;
  flex-direction: column;
  align-items: center;

  height: fit-content;
}
.resetPasswordRequestHeading {
  margin-top: 5%;
  font-size: 32px;
  font-weight: bold;
}
.resetPasswordEmailIdInput {
  width: 400px;
  font-family: Poppins;
  font-size: 14px;
  height: 40px;
  background-color: #e2e2e2;
  border: 0px;
  border-radius: 5px;
  margin-top: 4%;
  margin-bottom: 2%;
  padding: 2%;
  padding-left: 5%;
  outline: none;
  box-sizing: border-box;
}
.resetPasswordButtonDiv {
  margin-top: 4%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}
.resetPasswordButton {
  width: 160px;
  font-size: 14px;
  color: white;

  border: none;
  padding: 6%;
  border-radius: 50px;
  background-image: linear-gradient(0, #03a9fc, #006cff);
  transition-duration: 0.2s;
  cursor: pointer;
}
.resetPasswordButton:hover {
  color: black;
}
