.loginMainDiv {
  display: flex;
  flex-direction: column;
  width: 25%;
  margin: auto;
}

.loginHeading {
  font-size: 32px;
  font-weight: bold;
  margin-top: 5%;
}

.loginEmailIdInput,
.loginPasswordInput {
  font-family: Poppins;
  font-size: 14px;
  height: 40px;
  background-color: #e2e2e2;
  border: 0px;
  border-radius: 5px;
  margin-top: 4%;
  margin-bottom: 2%;
  padding: 2%;
  padding-left: 3%;
  outline: none;
  box-sizing: border-box;
}

.loginButtonDiv {
  margin-top: 4%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.loginButton {
  width: 100px;
  font-size: 18px;
  color: white;
  border: none;
  padding-top: 2%;
  padding-bottom: 2%;
  border-radius: 50px;
  background-image: linear-gradient(0, #03a9fc, #006cff);
  transition-duration: 0.2s;
  cursor: pointer;
}

.loginButton:hover {
  background-image: linear-gradient(0, #00aaff, #00a2ff);
}

.forgotPassDiv {
  display: flex;

  margin-top: 3%;
  font-size: 14px;
}
.dontHaveAccountTextDiv {
  display: flex;
  justify-content: center;
  margin-top: 3%;
  font-size: 14px;
}
.signupLink,
.forgotPassLink {
  text-decoration: underline;

  cursor: pointer;
}
@media screen and (max-width: 360px) {
  .loginMainDiv {
    width: 60%;
  }
}
