.navbarHomeMainDiv {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: solid 1px #dddedf;
  height: 65px;
}
.navBarHomeButton {
  width: 100px;
  font-size: 18px;
  color: white;
  border: none;
  padding: 5%;
  border-radius: 50px;
  background-image: linear-gradient(0, #03a9fc, #006cff);
  transition-duration: 0.2s;
  cursor: pointer;
  margin-left: 20px;
}
.navBarHomeButton:hover {
  background-image: linear-gradient(0, #00aaff, #00a2ff);
}
.navBarLogoutButton {
  width: 100px;
  font-size: 18px;
  color: white;
  border: none;
  padding: 5%;
  border-radius: 50px;
  background-image: linear-gradient(0, #03a9fc, #006cff);
  transition-duration: 0.2s;
  cursor: pointer;
  margin-left: 20px;
  margin-right: 10px;
}
.navBarLogout:hover {
  background-image: linear-gradient(0, #00aaff, #00a2ff);
}
.navBarHomeButtonsDiv {
  box-sizing: border-box;
  padding: 0.5%;
}
.appTitleHome {
  font-size: 32px;
  font-weight: bold;
  color: #1778f2;
  margin-left: 0.5%;
  text-decoration: none;
}
