.activateMainDiv {
  height: 100%;
  display: flex;
  justify-content: center;
}
.activateSubMainDiv {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.verifyHeading {
  margin-top: 5%;
  margin-bottom: 10%;
  font-weight: bold;
  font-size: 32px;
}
.verifyButton {
  width: 100px;
  font-size: 18px;
  color: white;
  font-weight: bold;
  border: none;
  padding-top: 2%;
  padding-bottom: 2%;
  border-radius: 50px;
  background-image: linear-gradient(0, #03a9fc, #006cff);
  transition-duration: 0.2s;
  cursor: pointer;
}

.verifyButton:hover {
  background-image: linear-gradient(0, #00aaff, #00a2ff);
}
