.homeCreateDMDiv {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  box-sizing: border-box;
  padding: 2%;
  border-bottom: solid 1px #dddedf;
  margin-bottom: 1%;
}
.homeUsernameInput {
  width: 60%;
  border-radius: 10px;
  padding: 2%;
  outline: none;
  border: none;
  background-color: #e2e2e2;
}
.homeCreateButton {
  width: 100px;
  font-size: 14px;
  color: white;
  border: none;
  padding-top: 2%;
  padding-bottom: 2%;
  border-radius: 50px;
  background-image: linear-gradient(0, #03a9fc, #006cff);
  transition-duration: 0.2s;
  cursor: pointer;
}
