.signupMainDiv {
  display: flex;
  flex-direction: column;
  width: 25%;
  margin: auto;
}

.signupHeading {
  font-size: 32px;
  font-weight: bold;
  margin-top: 5%;
}

.signupNameInput,
.signupEmailIdInput,
.signupPasswordInput {
  font-family: Poppins;
  font-size: 14px;
  height: 40px;
  background-color: #e2e2e2;
  border: 0px;
  border-radius: 5px;
  margin-top: 2%;
  margin-bottom: 1%;
  padding: 2%;
  padding-left: 5%;
  outline: none;
  box-sizing: border-box;
}

.signupButtonDiv {
  margin-top: 4%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.signupButton {
  width: 100px;
  font-size: 18px;
  color: white;

  border: none;
  padding-top: 2%;
  padding-bottom: 2%;
  border-radius: 50px;
  background-image: linear-gradient(0, #03a9fc, #006cff);
  transition-duration: 0.2s;
  cursor: pointer;
}

.signupButton:hover {
  background-image: linear-gradient(0, #00aaff, #00a2ff);
}

.alreadyHaveAccountTextDiv {
  display: flex;
  justify-content: center;
  margin-top: 3%;
  font-size: 14px;
}

.signupLink {
  text-decoration: underline;

  cursor: pointer;
}
